@font-face {
  font-family: "Magistral";
  font-display: swap;
  font-style: normal;
  font-weight: 300;
  src: local("Magistral-Light"),
    url("./fonts/Magistral-Light.woff2") format("woff2"),
    url("./fonts/Magistral-Light.woff") format("woff"),
    url("./fonts/Magistral-Light.ttf") format("truetype");
}

