.contactsContainer {
  height: 100vh;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contactsTitle {
  font-size: 32px;
  font-weight: 800;
  margin: 0;
  align-self: flex-start;
  line-height: 26px;
  letter-spacing: 1.5px;
}

.contactsSubtitle {
  font-size: 32px;
  font-weight: 800;
  margin: 0;
  align-self: flex-start;
  line-height: 26px;
  letter-spacing: 1.5px;
  color: #e65d18;
}

.contactsTable {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

.contactsTableCol {
  width: 50%;
}

.contactsTableTitle {
  font-size: 26px;
  margin: 0;
  display: flex;
  justify-content: flex-start;
}

@media screen and (max-width: 1280px) {
  .contactsTable {
    flex-direction: column;
  }
}

@media screen and (max-width: 1280px) {
  .contactsTitle,
  .contactsSubtitle,
  .contactsTableTitle {
    font-size: 24px;
    font-weight: 600;
    display: block;
    align-self: center;
  }
  .contactsTitle {
    padding-top: 40px;
  }
  .contactsTableCol {
    width: 65%;
  }
}
@media screen and (max-width: 1000px) {
  .contactsContainer {
    height: max-content;
  }
}
