.productItemContainer {
  color: white;
  width: 400px;
  height: 250px;
  background-color: #1c3c5b;
  border-radius: 3%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.productItemTitle {
  font-size: 26px;
  margin: 0;
  font-weight: bold;
  align-self: left;
  padding-bottom: 20px;
}

.productItemInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  gap: 20px;
}

.productItemImg {
  background-size: contain;
  background-repeat: no-repeat;
  width: 120px;
}

.productItemImgContainer {
  background-size: contain;
  background-repeat: no-repeat;
  width: 120px;
  /* height: 120px; */
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.productItemСharacteristics {
  width: 80%;
  padding: 0;
}
.productItemPoint {
  text-align: left;
  list-style: none;
  font-size: 14px;
  padding-bottom: 5px;
  display: flex;

  /* height: 15px; */
  overflow: hidden;
  text-overflow: ellipsis;
}

.productItemDescription {
  margin: 0;
  padding-left: 5px;
  height: 17px;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 50%;
}

.productItemSubtitle {
  font-weight: 700;
  margin: 0;
  display: inline;
  width: max-content;
  max-width: 50%;
  overflow-wrap: break-word;
}

.productItemMore {
  background-color: #e65d18;
  border: 0px;
  border-radius: 20px;
  box-shadow: 0px 0px 2px #e65d18;
  cursor: pointer;
  color: white;
  font-size: 20px;
  font-weight: 400;
  margin-top: 10px;
  padding: 5px 20px;
}

@media screen and (max-width: 900px) {
  .productItemContainer {
    height: 300px;
  }
}
